import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";
import Banner from "src/components/Banner";

export interface IndexProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 50px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        margin-bottom: 60px;
        & > div:nth-child(1) {
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
            letter-spacing: normal;
            color: #000000;
            margin-bottom: 60px;
        }

        & > div:nth-child(2) {
            display: flex;
            justify-content: space-between;
        }
    }
`;

const Item = styled.div`
    width: 23.5%;
    padding-bottom: 20%;
    height: 0;
    color: white;
    position: relative;

    & > div {
        background-color: black;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        padding: 12px;

        display: flex;
        flex-direction: column;
        &.poly {
            clip-path: polygon(0% 0%, 85% 0%, 100% 50%, 85% 100%, 0% 100%);
        }

        & > div:nth-child(1) {
            font-size: 22px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #ffffff;
            margin-bottom: 18px;
        }

        & > div:nth-child(2) {
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #ffffff;
            margin-bottom: 14px;
        }

        & > div:nth-child(3) {
            font-size: 15px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #ffffff;
        }
    }
`;

export default class Index extends React.Component<IndexProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "프로젝트", link: "/project/" }, { name: "부동산 개발", link: "/project" }]}
                />
                <Content>
                    <Banner>
                        <img src={require("../../assets/project/project-banner.png")} alt="" />
                    </Banner>
                    <Desc>
                        <div>
                            <div>공간 개발 프로젝트</div>
                            <div>
                                <Item>
                                    <div className="poly">
                                        <div>01</div>
                                        <div>투자 대상 발굴</div>
                                        <div>
                                            이동이 편리한 교통 중심 지역 중, <br /> 상대적으로 낙후된 종합 상권의 <br />
                                            소형 빌딩 중심으로 투자 대상을 <br />
                                            조사합니다.
                                        </div>
                                    </div>
                                </Item>
                                <Item>
                                    <div className="poly">
                                        <div>02</div>
                                        <div>대상 건물 매입</div>
                                        <div>
                                            투자 대상 건물을 직접 또는 <br />
                                            투자 유치를 통해 매입합니다.
                                        </div>
                                    </div>
                                </Item>
                                <Item>
                                    <div className="poly">
                                        <div>03</div>
                                        <div>지역특화 공간 리모델링</div>
                                        <div>
                                            대상 건물 지역의 배후 타겟과 <br />
                                            로컬의 소비 트렌드에 적합한 <br />
                                            공간 컨셉을 설정하고 리모델링 <br />
                                            또는 재건축합니다.
                                        </div>
                                    </div>
                                </Item>
                                <Item>
                                    <div>
                                        <div>04</div>
                                        <div>MD 기획에 따른 공간 운영</div>
                                        <div>
                                            자사 계열 브랜드의 입점 또는 <br />
                                            청년 인큐베이팅 파트너와의 <br />
                                            협업을 통해 공간을 직접 <br />
                                            운영합니다.
                                        </div>
                                    </div>
                                </Item>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



